import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addTag } from './actions'
//import { Tag } from './components'

class Tags extends React.Component{

	constructor(props) {
		super(props)

		this.handleChange = this.handleChange.bind(this)
		this.removeTag = this.removeTag.bind(this)
		this.addTag = this.addTag.bind(this)

		this.state = {
			tag : ''
		}

	}

	removeTag(e){
		e.preventDefault()
		this.props.handleRemoveTag(e.target.value)
	}

	clearField(e){
		e.preventDefault()

		setState({
				tag : ''
			}
		)
	}

	addTag(e) {
		e.preventDefault()

		this.props.addTag(this.state.tag)
	}

	handleChange(event) {
		this.setState({
			tag: event.target.value
		})
	}

	render(){
		return(
			<div className="form">
				<form onSubmit={this.addTag}>
					Add New Tags
					<input type="text"  name="tags" onChange={this.handleChange} placeholder="Tag Name" />
				</form>
				<ul id="tags">
					{this.props.tags.tags.map((tag,i) => {
						return (
							<li key={i}>
								<button value={tag} > {tag} </button>
								</li>
						)
					})}
				</ul>

			</div>
		)
	}

}

Tags.propTypes = {
	tags : PropTypes.object,
	addTag: PropTypes.func,
	handleRemoveTag: PropTypes.func,
	handleChange : PropTypes.func,
}

export default connect(
	state => ({
		tags: state.tags
	}), {
		addTag
	}
)(Tags)



